.form__group {
    position: relative;
    padding: 15px 0 0;
    margin-top: 10px;
    width: 50%;
  }
  
  .form__field {
    font-family: inherit;
    width: 100%;
    border: 0;
    border-bottom: 2px solid #9b9b9b;
    outline: 0;
    font-size: 1.3rem;
    color: #333;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;
  }
  .form__field::-webkit-input-placeholder {
    color: transparent;
  }
  .form__field:-ms-input-placeholder {
    color: transparent;
  }
  .form__field::placeholder {
    color: transparent;
  }
  .form__field:-ms-input-placeholder ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
  .form__field:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
  
  .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #9b9b9b;
  }
  
  .form__field:focus {
    padding-bottom: 6px;
    font-weight: 700;
    border-width: 3px;
    border-image: linear-gradient(to right, #11998e, #38ef7d);
    border-image-slice: 1;
  }
  .form__field:focus ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #11998e;
    font-weight: 700;
  }
  
  /* reset input */
  .form__field:required, .form__field:invalid {
    box-shadow: none;
  }
  


  .form_button {
    /* width: 300px; */
    /* font-family: "Bungee", cursive; */
    font-size: 1.2rem;
    position: relative;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    cursor: pointer;
    background: rgb(112, 136, 216);
    border: 2px solid #333dcb;
    padding: 15px;
    border-radius: 45px;
    color: white;
    overflow: hidden;
    box-shadow: 0 2px 4px darkgray;
    transition: all 0.2s ease-in;
    z-index: 1;
    text-align: center;
  }
  .form_button .circle {
    position: absolute;
    border-radius: 50%;
    pointer-events: none;
    background-image: radial-gradient(circle closest-side, white, #adb6ea);
    width: 1px;
    height: 1px;
    z-index: 3;
    animation-name: ripple;
    animation-duration: 0.75s;
    animation-timing-function: ease-in;
  }
  .form_button:hover {
    transform: scale(1.01);
    box-shadow: 0 6px 4px darkgray;
  }
  
  @keyframes ripple {
    0% {
      transform: scale(0, 0);
      opacity: 0;
    }
    75% {
      transform: scale(450, 450);
      opacity: 0.75;
    }
    100% {
      transform: scale(700, 700);
      opacity: 0;
    }
  }
